@import "ember-modal-dialog/ember-modal-structure";
@import "ember-modal-dialog/ember-modal-appearance";

.modal-content {
  display: flex;
  flex-direction: column;
  
  .image-quote {
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;

    .modal-left {
      max-height: 70vh;
      display: inline-block;
      img {
        margin: 0;
        max-height: 70vh;
      }
    }
  
    .modal-right {
      position: absolute;
      top: 0;
      left: 0;
      padding: 25px;

      .quote {
        width: 100%;
  
        h2 {
          padding: 20px;
          line-height: 45px;
          font-size: 35px;
          width: 100%;
          color: white;
          transform: rotate(-3deg);
          text-shadow: 0 5px 30px rgba(30, 30, 30, 0.5);
        }
      }
    }
  }

  .info {
    h2, h3, p, a, li {
      color: rgba(20, 20, 20, 0.7);
    }

    .info-header {
      font-family: 'Source Sans Pro';
      text-transform: uppercase;
    }

    .description {
      margin-bottom: 15px;
    }

    .info-data {
      display: inline;
    }

    .other-info {
      flex-wrap: wrap;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;

      .click-to-show {
        flex: 1;
        margin-right: 15px;
        margin-bottom: 15px;
      }
    }
  }
}

.ember-modal-wrapper.emd-animatable.emd-wrapper-target-attachment-center .ember-modal-overlay {
  background-color: rgba(20, 20, 20, 0.7);
}
.ember-modal-dialog {
  box-shadow: 0 15px 50px 0 $dark-gray;
  margin-top: 15px;
  padding: 25px;
  background-color: lighten($tan, 10)
}

.lazy-image-container {
  background: rgba(30, 30, 30, 0.8)
}

.click-to-show {
  .label {
    cursor: pointer;
  }
  .info-header {
    text-transform: capitalize;
    transition: all .2s ease;
    margin-bottom: 5px;
  }
  .info-data {
    transition: all .2s ease;
    line-height: 20px;
    height: 20px;
  }
}